.works{
    background-color: #060f18;
    font-size: 30px;
    margin:0px;
    box-shadow: 0 0 20px 0px #386a82;
    .works_Title{
        font-size: 30px;
        padding-top: 30px;
    }
    .box_works{
        display: flex;
        justify-content: space-around;
        margin:  auto;
        width: 80%;
        /*border: 1px solid white;*/
        flex-wrap: wrap;
        text-align: center;
        padding: 40px;
        padding-top: 0px;

        .work{
            display: flex;
            flex-direction: column;
            font-family: Teko,Arial,Helvetica,sans-serif;
            /*border: 1px solid aquamarine;*/
            border-radius: 5px;
            color: #386a82;
            padding: 60px;
            width: 500px;
            box-shadow: inset 0 0 20px 20px rgb(15,30,41);
            @media screen and (min-width: 350px){
                padding: 20px;
                width: 300px;
            }
        }


    }
}

